import "./App.css";
import {
  RouterProvider,
  createBrowserRouter,
  createHashRouter,
  Navigate,
} from "react-router-dom";
import Profile from "./pages/Profile";
import About from "./pages/About";
import Articles from "./pages/Articles";
import Projects from "./pages/Projects";
import Research from "./pages/Research";
import Speaking from "./pages/Speaking";
import Music from "./pages/Music";
import Uses from "./pages/Uses";
import CV from "./pages/CV";
import Root from "./pages/Root";
import Error from "./pages/Error";
import { useEffect, useState } from "react";

function App() {
  const [darkMode, setDarkMode] = useState(
    localStorage.theme !== "light"
  );

  useEffect(() => {
    if (darkMode) {
      localStorage.theme = "dark";
      document.documentElement.classList.add("dark");
      document.documentElement.style = "color-scheme: dark;";
    } else {
      localStorage.theme = "light";
      document.documentElement.classList.remove("dark");
      document.documentElement.style = "color-scheme: light;";
    }
  }, [darkMode]);

  // useEffect(() => {
  //   if (darkMode) {
  //     localStorage.theme = "dark";
  //     document.documentElement.classList.add("dark");
  //   } else {
  //     localStorage.theme = "light";
  //     document.documentElement.classList.remove("dark");
  //   }
  // }, []);

  const toggleDarkMode = () => setDarkMode((oldState) => !oldState);

  const router = createHashRouter([
    {
      path: "/",
      element: <Root darkMode={darkMode} toggleDarkMode={toggleDarkMode} />,

      children: [
        {
          path: "",
          // element: <Profile />,
          element: <Navigate to="/about" replace />,
        },
        {
          path: "/about",
          element: <About />,
        },
        // {
        //   path: "/blog",
        //   element: <Articles />,
        // },
        {
          path: "/research",
          element: <Research />,
        },
        {
          path: "/music",
          element: <Music />,
        },
        {
          path: "/cv",
          element: <CV />,
        },
        {
          path: "*",
          element: <Error />,
        },
      ],
    },
  ]);

  return <RouterProvider router={router} />;
}

export default App;
